@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');
/* Base */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #333;
  color: #aaa;
  font-family: 'Montserrat', sans-serif;
}

/* Header */
header {
  background-color: #222;
  color: #ddd;
  padding: 1.2rem;
}

// Container
.container {
  max-width: 1200px;
  margin: auto;
}

// Tabs
.react-tabs {
  .react-tabs__tab-panel--selected {
    height: 500px;
  }
  ul {
    width: 100%;
    background-color: #444;
    padding: 0.6rem;
    position: fixed;
    bottom: 0;
    list-style-type: none;
    display: grid;
    grid-auto-flow: column;
    justify-items: center;
  }
}

// Folder picker
.folder-select {
  width: 60vw;
  margin: auto;
  text-align: center;

  .custom-input {
    background-color: #444;
    border: 1px solid #aaa;
    border-radius: 4px;
    display: inline-block;
    padding: .5rem;
    cursor: pointer;
  }

  .file-input {
    display: none;
  }
}
