// Player
.player {
  display: grid;
  grid-template-columns: 1fr;
}

.album-art {
  width: 100%;
  position: relative;
  z-index: 1;

  img {
    @extend .album-art;
    z-index: 2;
  }
}

.song-info {
   h3 {
     margin-bottom: 1rem;
   }
}

.back {
  color: #f5f5f5;
  font-size: 1.5rem;
  display: inline-block;
  position:absolute;
  left: 0.35rem;
  top: 0.15rem;
  z-index: 3;
}

.tools {
  font-size: .8rem;
  text-align: right;
}

.speed {
  border: 1px solid #aaa;
  border-radius: 3px;
  display: inline-block;
  padding: 0.2rem;
  margin-bottom: 1rem;
}

.bottom {
  padding: 0.75rem;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 1.25rem;
}

.controls {
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 1.25rem;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 1rem;
}

.controls a {
  color: #ccc;
  text-align: center;
  text-decoration: none;
}

.seeker {
}

.seeker {
  background: #f2b43b;
  -webkit-appearance: none;
  grid-column: 1 / -1;
  margin: 18px 0;
  width: 100%;

  &:focus {
    outline: none;
  }

  &::-webkit-slider-runnable-track {
    width: 100%;
    height: 0.25rem;
    cursor: pointer;
    animate: 0.2s;
    /* box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; */
    background: #f2b43b;
    border-radius: 1.3px;
    /* border: 0.2px solid #010101; */
  }

  &::-webkit-slider-thumb {
    /* box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; */
    /* border: 1px solid #000000; */
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background: #f2b43b;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -8px;
  }

  &:focus::-webkit-slider-runnable-track {
    background: #f2b43b;
  }

  &::-moz-range-track {
    width: 100%;
    height: 25rem;
    cursor: pointer;
    animate: 0.2s;
    /* box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; */
    background: #f2b43b;
    border-radius: 1.3px;
    /* border: 0.2px solid #010101; */
  }

  &::-moz-range-thumb {
    /* box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; */
    border: 1px solid #000000;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background: #f2b43b;
    cursor: pointer;
  }

  &::-ms-track {
    width: 100%;
    height: 25rem;
    cursor: pointer;
    animate: 0.2s;
    background: transparent;
    border-color: transparent;
    border-width: 16px 0;
    color: transparent;
  }

  &::-ms-fill-lower {
    background: #f2b43b;
    /* border: 0.2px solid #010101; */
    border-radius: 2.6px;
    /* box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; */
  }

  &::-ms-fill-upper {
    background: #f2b43b;
    /* border: 0.2px solid #010101; */
    border-radius: 2.6px;
    /* box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; */
  }

  &::-ms-thumb {
    /* box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; */
    /* border: 1px solid #000000; */
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background: #f2b43b;
    cursor: pointer;
  }

  &:focus::-ms-fill-lower {
    background: #f2b43b;
  }

  &:focus::-ms-fill-upper {
    background: #f2b43b;
  }
}

.shuffle {
  font-size: 1.25rem;
}
.repeat {
  @extend .shuffle;
}

.skip-back {
  font-size: 1.5rem;

}
.skip-forward {
  @extend .skip-back;
}

.play {
  font-size: 2rem;
}

.controls {
  a {
    &:hover {
      color: #777;
    }
  }
}

@media screen and (min-width: 430px) {
  .player {
      grid-template-columns: 1fr 2fr;
      padding: 1rem;
  }
}